<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="text-right m-3">
            <button class="btn btn-sm btn-primary" @click="openModal(true)"></button>
        </div>
        <div class="table-responsive pt-3">
            <table class="table table-striped table-sm">
                <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="item in coupons" :Key="item.id">
                    <td>{{item.title}}</td>                
                    <td>{{item.code}}</td>
                    <td>{{item.percent}}</td>
                    <td>{{item.due_date}}</td>
                    <td>
                        <span v-if="item.is_enabled"></span>
                        <span v-else></span>
                    </td>
                    <td>
                        <div class="btn-group">
                            <button class="btn btn-sm btn-outline-primary" @click="openModal(false,item)"></button>
                            <button class="btn btn-sm btn-outline-danger" @click="openDelModal(item)"></button>
                        </div>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
        <!--  Modal -->
        <CouponModal :coupon-title="titleModal" :coupon="couponData" @updataClick="updataProduct"/>
        <!--  -->
        <!--  Modal -->
        <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white py-2">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body text-center py-4">
                     <strong class="text-danger">{{ couponData.code }}</strong>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-danger" @click="deleteProduct(couponData)"></button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery'
import CouponModal from './modal/CouponModal'

export default {
    components: {
        CouponModal
    },
    data() {
        return {
            coupons: [], //
            couponData: {}, //
            isNew: false,
            titleModal: '',
            isLoading: false,
            paginationData: {}
        };
    },
    methods: {
        getCoupons() { //
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons`;
            const vm = this;
            vm.isLoading = true
            this.$http.get(api).then(response => {
                console.log(response.data);
                vm.isLoading = false
                vm.coupons = response.data.coupons
                vm.paginationData = response.data.pagination
                if(!response.data.success) {
                    this.$bus.$emit('message:push',response.data.message,'danger');
                }                       
            });
        },
        
        openModal(isNew,item){  // Modal
            // console.log('isNew:'+isNew, 'item:'+item)          
            if(isNew) {
                this.couponData = {}
                this.isNew = true
            }else{
                this.titleModal = ''
                this.couponData = Object.assign({}, item) // item  couponData 
                this.isNew = false                
            }
            $('#productModal').modal('show')
        },
        
        updataProduct(){ //
            let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
            let httpMethod = 'post'
            const vm = this;
            if(!vm.isNew){
                api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.couponData.id}`;
                httpMethod = 'put'
            }
            this.$http[httpMethod](api, {data: vm.couponData}).then(response => {
                // console.log(response.data);
                if(response.data.success) {
                    $('#productModal').modal('hide')
                    vm.getCoupons()
                }else {
                    vm.getCoupons()
                    this.$bus.$emit('message:push',response.data.message,'danger');
                    // console.log('')
                }
            });
        },
        openDelModal(item){ // Modal
            this.couponData = item
            $('#delCouponModal').modal('show')
        },
        deleteProduct(){ //
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${this.couponData.id}`;
            const vm = this;
            this.$http.delete(api).then(response => {          
                if(response.data.success) {
                    $('#delCouponModal').modal('hide')
                    vm.getCoupons()
                }else {
                    vm.getCoupons()
                    this.$bus.$emit('message:push',response.data.message,'danger');
                    // console.log('')
                }
            });
        },
    },
    created() {
        this.getCoupons();
    }
}
</script>

<style lang="scss" scoped>
.itemImg {
    max-width: 100%;
}
tbody tr td{
    vertical-align: middle;
}
</style>


